<template>
	<div id="order-comment">
		<public-header title="添加评论"></public-header>
		<div class="content">
			<template v-if="order.order_type == 1">
				<div class="content-block-title">配送评价</div>
				<van-cell-group>
					<van-cell>
						<div class="flex" slot="title">
							<span class="van-cell-text">配送服务</span>
							<van-rate class="flex" v-model="delivery_service" :size="20" :count="5" color="#ff2d4b" void-color="#FF6C81"/>
						</div>
					</van-cell>
				</van-cell-group>
			</template>
			<div class="content-block-title">商品评价</div>
			<van-cell-group>
				<van-cell>
					<div class="flex" slot="title">
						<span class="van-cell-text">商品质量</span>
						<van-rate class="flex" v-model="goods_quality" :size="20" :count="5" color="#ff2d4b" void-color="#FF6C81"/>
					</div>
				</van-cell>
				<van-cell :title="item.goods_title" v-for="(item, index) in goods" :key="index">
					<div slot="right-icon" class="favor-oppose">
						<label @click="favorOppose(index, 'favor')">
							<input type="radio" class="radio" value="1" :checked="item.activity == 1">
							<span class="favor"></span>
						</label>
						<label @click="favorOppose(index, 'oppose')">
							<input type="radio" class="radio" value="2" :checked="item.activity == 2">
							<span class="oppose"></span>
						</label>
					</div>
				</van-cell>
			</van-cell-group>
			<div class="content-block-title">写点什么</div>
			<w-textarea v-model="note" :tools="tools" ref="textarea" />
			<div class="goods-tag van-hairline--top">
				<div class="goods-tag-item" v-for="(item, index) in goods" :key="index" @click="onAddGoodsTag(item)">{{item.goods_title}}</div>
			</div>
			<div class="content-block-title">有图有真相</div>
			<uploader @onGetUrl="onGetImgsUrl" :value="thumbs" :max="6"></uploader>
			<div class="save-btn">
				<van-button size="large" @click="onSubmit()">提交评论</van-button>
			</div>
		</div>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import PublicHeader from '@/components/header'
	import Uploader from '@/components/uploader'
	export default {
		components: {
			PublicHeader,
			Uploader
		},
		data() {
			return {
				id: Number,
				delivery_service: 0,
				goods_quality: 0,
				note: ' ',
				goods: [],
				thumbs: [],
				newGoods: [],
				order: {},
				comment: {
					id: 0
				},
				tools: [],
				showPreLoading: true
			}
		},
		methods: {
			onAddGoodsTag(goods) {
				var that = this;
				var DOM = that.$refs.textarea;
				var text = goods.goods_title;
				DOM.addTag(text)
				that.keepLastIndex(DOM.$refs.wTextareaContent);
			},


			keepLastIndex(obj) {
				/*console.log(obj)
				console.log(window.getSelection)
				console.log(document.selection)*/
				if (window.getSelection) { //ie11 10 9 ff safari
					obj.focus(); //解决ff不获取焦点无法定位问题
					var range = window.getSelection(); //创建range
					range.selectAllChildren(obj); //range 选择obj下所有子内容
					range.collapseToEnd(); //光标移至最后
				} else if (document.selection) { //ie10 9 8 7 6 5
					var range = document.selection.createRange(); //创建选择对象
					//var range = document.body.createTextRange();
					range.moveToElementText(obj); //range定位到obj
					range.collapse(false); //光标移至最后
					range.select();
				}
			},

			onLoad() {
				if(this.$route.query.id) {
					this.id = this.$route.query.id;
				} else {
					this.$toast('订单不存在或已删除！')
				}
				this.util.request({
					url: 'wmall/order/comment/index',
					data: {
						id: this.id
					}
				}).then((res) => {
					this.showPreLoading = false;
					let result = res.data.message;
					if(result.errno) {
						this.$toast(result.message)
						return;
					}
					console.log(result)
					this.order = Object.assign(this.order, result.message.order);
					if(result.message.goods) {
						this.goods = [...this.goods, ...result.message.goods]
					}
					this.goods.forEach((item, index) => {
						let goodsItem = {
							id: item.id,
							goods_id: item.goods_id,
							activity: item.activity,
						}
						this.newGoods.push(goodsItem);
					})
					this.comment = this.util.extend(this.comment, result.message.comment);
					if(this.comment && this.comment.id > 0) {
						this.delivery_service = this.comment.delivery_service;
						this.goods_quality = this.comment.goods_quality;
						this.note = this.comment.note;
						this.thumbs = [...this.comment.thumbs];
					}
					var DOM = this.$refs.textarea;
					this.keepLastIndex(DOM.$refs.wTextareaContent);
				});
				if(this.util.isGlala()) {
					this.util.requestPermissionList({
						perm: ['storage', 'camera', 'photos']
					});
				}
			},

			favorOppose(index, type) {
				var index = index;
				var type = type;
				if(type == 'favor') {
					this.goods[index].activity = 1;
					this.newGoods[index].activity = 1;
				} else {
					this.goods[index].activity = 2;
					this.newGoods[index].activity = 2;
				}
			},
			onGetImgsUrl(value) {
				this.thumbs = value;
			},
			onSubmit() {
				if(this.order.order_type == 1 && !this.delivery_service) {
					this.$toast('请您评价配送服务');
					return false;
				}
				if(!this.goods_quality) {
					this.$toast('请您评价商品质量');
					return false;
				}
				var params = {
					id: this.id,
					note: this.note,
					goods_quality: this.goods_quality,
					deliverScore: this.delivery_service,
					thumbs: this.thumbs,
					goods: this.newGoods,
				}
				this.util.request({
					url: 'wmall/order/comment/post',
					method: 'post',
					data: params
				}).then((res) => {
					let result = res.data.message;
					if(result.errno) {
						this.util.$toast(result.message)
						return false;
					} else {
						if(this.comment && this.comment.id > 0) {
							this.util.$toast('修改成功', this.util.getUrl({path: '/pages/member/comment', query: {}}), 1500, 'replace');
						} else {
							this.util.$toast(result.message, this.util.getUrl({path: '/pages/order/detail', query: {id: this.id}}), 1500, 'replace');
						}
					}
				})

			}
		},
		mounted() {
			this.onLoad()
		}
	}
</script>

<style>
	#order-comment .w-textarea{
		background-color: #fff;
		border: 0px!important;
		margin-bottom: 0px!important;
	}
	#order-comment .w-textarea_input{
		height: 100px!important;
		overflow-y: scroll!important;
		overflow-x: inherit!important;
	}

	#order-comment .w-textarea wise{
		color: #ff2d4b;
		white-space: inherit!important;
		margin: 0 1px;
	}
	#order-comment .goods-tag{
		display: flex;
		flex-flow: row wrap;
		background-color: #fff;
		padding: 5px;
		font-size: 12px;
	}
	#order-comment .goods-tag .goods-tag-item{
		padding: 5px;
		border-radius: 3px;
		border: 1px solid #ccc;
		margin: 5px;
	}

	#order-comment .content .content-block-title{
		position: relative;
		overflow: hidden;
		font-size: 14px;
		line-height: 1;
		color: #6d6d72;
		text-overflow: ellipsis;
		text-transform: uppercase;
		white-space: nowrap;
		margin: 10px 15px;
	}
	#order-comment .content .van-cell-group .van-cell-text{
		padding-right: 10px;
		font-size: 15px;
	}
	#order-comment .content .favor-oppose span {
		display: inline-block;
		width: 45px;
		height: 28px;
		margin-bottom: -7px;
	}
	#order-comment .content .favor-oppose input[type=radio] {
		display: none;
	}
	#order-comment .content .favor-oppose .favor {
		background: url(../../../static/img/favor_grey.png) no-repeat;
		background-size: cover;
	}
	#order-comment .content .favor-oppose input[type=radio]:checked + .favor {
		background: url(../../../static/img/favor.png) no-repeat;
		background-size: cover;
	}
	#order-comment .content .favor-oppose .oppose {
		background: url(../../../static/img/oppose_grey.png) no-repeat;
		background-size: cover;
	}
	#order-comment .content .favor-oppose input[type=radio]:checked + .oppose {
		background: url(../../../static/img/oppose.png) no-repeat;
		background-size: cover;
	}
	#order-comment .content .img-container{
		background-color: #fff;
		padding: 10px;
		padding-bottom: 0;
	}
	#order-comment .content .img-container .van-col{
		position: relative;
		height: 60px;
		margin-bottom: 10px;
	}
	#order-comment .content .img-container .van-col img {
		width: 100%;
		height: 100%;
		border-radius: 3px;
	}
	#order-comment .content .img-container .van-col .icon{
		position: absolute;
		width: 20px;
		height: 20px;
		border-radius: 50%;
		background: rgba(0,0,0,.6);
		right: -5px;
		top: -10px;
		z-index: 10000;
		color: #fff;
		text-align: center;
		font-size: 14px;
		line-height: 20px;
	}
	#order-comment .content .img-container .van-col .van-uploader{
		width: 100%;
		height: 100%;
	}
	#order-comment .content .save-btn{
		margin: 10px;
	}
	#order-comment .content .save-btn .van-button{
		background-color: #ff2d4b;
		color: #fff;
		border-radius: 5px;
	}
</style>



